
* {
  font-family: Arial, Helvetica, sans-serif;
}
body{
  padding: 60px;
}
input {
  width: 100%;
  font-size: 1.3rem;
  color: grey;
}
.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin: 0px 10px 10px 0px;
}
.card p {
  padding: 5px;
  text-align: center;

}
.card img {
  max-height: 170px;
}
.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}